@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --primary:  #ff8008 !important;
    --secondary:  #ffc837 !important;
    
  }

  html{ font-family: 'Roboto', sans-serif;}

  .text-pri { color:#ff8008 !important; color:var(--primary) !important; }

  .hover-bg-pri:hover { background: #ff8008 !important; background: var(--primary) !important; color:#fff !important; }

   li{ cursor:pointer; font-size:20px !important; transition:font-size 1s ease-in-out;}


  .brand{
     position:relative;
     font-size:100px !important;
     font-weight:600;
    }

    .brand::after, .brand::before{
        content:'';
        display:block;
        position: absolute;
        bottom:1px;
        left:8px;
        width:0;
        height:0;
        border-left:9px solid transparent !important;
        border-right: 10px solid transparent !important;
        border-bottom:80px solid #ff8008 !important;
        border-bottom:80px solid var(--primary) !important;
        transform:rotate(7deg);
        z-index:222;
    }

    .brand::before{
        z-index:999;
    }
  
  .submit_container{
     width:100%;
   }
  
   form{
     width:75%;
     margin:0 auto;
   }
  
   .fa-angle-down{ font-size:30px !important; cursor:pointer;}
  
  .w{ width:10%; height:10px!important; border-radius:10px; background:#ff8008!important; background:var(--primary)!important; margin:0 auto; }
  
  .cus-input,input{
    width:100%;
    border-radius:25px !important;
    border:2px solid #ff8008 !important;
    border:2px solid var(--primary) !important;
    height:50px !important;
    outline:none !important;
    transition:transform 0.4s linear;
  }
   
  
  input[type="submit"]:active,button:active{
    transform:scale(0.8); transition:transform 0.4s linear;
  } 
  
  .arrow-up {
  position:absolute;
  width: 0;
    height: 0;
    border-left: 80px solid transparent;
    border-right: 80px solid transparent;
    border-bottom: 220px solid #ff8008 !important;
    border-bottom: 220px solid var(--primary) !important;
   left:50%;
   transform:translateX(-50%);  
   bottom:-60px;
    
  }
 
  .h{
    position:relative;
    height:100vh !important;
   overflow-y:hidden !important;
  
  }
  
  .width{
      position:relative;
      width:300px;
 margin:0 auto !important;
     top:25% !important;
   }


    
    li:active{ font-size:16px !important; transition:font-size 1s ease-in-out; }
   
  
  
  .bg-pri{ background: #ff8008 !important; background: var(--primary) !important;  color:#fff !important;}
  
  
   @media screen and (min-width:576px){
     .submit_container{ margin:0 auto !important; }
   }
  
   @media screen and (min-width:768px){
      .submit_container{ width:640px; max-width:800px !important; }
       
       }
 
